@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(/react-mesto-auth/static/media/Inter-Regular.dc131113.woff2) format("woff2"),
    url(/react-mesto-auth/static/media/Inter-Regular.3ac83020.woff) format("woff");
}

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url(/react-mesto-auth/static/media/Inter-Medium.21d08187.woff2) format("woff2"),
    url(/react-mesto-auth/static/media/Inter-Medium.796494d6.woff) format("woff");
}

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url(/react-mesto-auth/static/media/Inter-Black.661569af.woff2) format("woff2"),
    url(/react-mesto-auth/static/media/Inter-Black.d0b121f3.woff) format("woff");
}

.page {
  background-color: #000;
  font-family: "Inter", Arial, sans-serif;
}

.page__container {
  margin: 0 auto;
  max-width: 1280px;
  min-width: 320px;
  min-height: 100vh;
}

.header {
  margin: 0 auto;
  padding-top: 45px;
  max-width: 69%;
  border-bottom: 1px solid #545454;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
@media screen and (max-width: 600px) {
  .header {
    padding: 0;

    max-width: 100%;
    flex-direction: column-reverse;
    justify-content: center;
  }
}

.header__logo {
  margin-bottom: 41px;
  width: 142px;
  height: 33px;
}

@media screen and (max-width: 600px) {
  .header__logo {
    margin-top: 27px;
    margin-bottom: 32px;
    margin-left: 27px;
    width: 104px;
    height: 24px;
  }
}

.header__link {
  text-decoration: none;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  color: #ffffff;
  cursor: pointer;
  margin-bottom: 46px;
  padding: 0;
}

@media screen and (max-width: 600px) {
  .header__link {
    margin-top: 34px;
    margin-bottom: 33px;
  }
}

.header__link_grey {
  background: none;
  border: none;
  padding: 0;
  color: #a9a9a9;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
}

.header__user-email {
  margin: 0;
  margin-right: 24px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
}
@media screen and (max-width: 600px) {
  .header__user-email {
    margin-bottom: 18px;
    margin-right: 0;
  }
}

.header__user-info {
  display: flex;
  justify-content: center;
  margin-bottom: 46px;
}
@media screen and (max-width: 600px) {
  .header__user-info {
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 0;
    padding-bottom: 40px;
    border-bottom: 1px solid #545454;
  }
}

.profile {
  margin: 0 auto;
  margin-top: 40px;
  max-width: 69%;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 900px) {
  .profile {
    flex-direction: column;
    align-items: center;
  }
}

.profile__box {
  display: flex;
  margin: 0;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .profile__box {
    flex-direction: column;
  }
}

.profile__avatar {
  width: 120px;
  height: 120px;
  margin-right: 30px;
  border-radius: 50%;
  background-color: #000;
  object-fit: cover;
}

@media screen and (max-width: 768px) {
  .profile__avatar {
    margin: 0 auto;
  }
}

.profile__avatar-change {
  background-image: url(/react-mesto-auth/static/media/avatar-edit.d476a8b8.svg);
  width: 120px;
  height: 120px;
  border-radius: 50%;
  position: absolute;
  opacity: 0;
  border: 0;
  background-repeat: no-repeat;
  background-position: center;
  transition: 0.8s ease;
}

.profile__avatar-change:hover {
  opacity: 0.8;
  background-color: #000;
}

.profile__info-container {
  margin: 0 auto;
  margin-top: 22px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.profile__info-name {
  max-width: 294px;
  margin: 0 auto;
  margin-right: 18px;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #fff;
  display: inline-block;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (max-width: 768px) {
  .profile__info-name {
    margin: 0 auto;
    max-width: 189px;
    margin-right: 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 27px;
    line-height: 33px;
  }
}

.profile__avatar-edit-button {
  width: 24px;
  height: 24px;
  background-image: url(/react-mesto-auth/static/media/edit-button.6bfdb354.svg);
  border: solid 1px #ffffff;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.profile__avatar-edit-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 768px) {
  .profile__avatar-edit-button {
    width: 18px;
    height: 18px;
  }
}

.profile__info-occupation {
  max-width: 336px;
  font-style: normal;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  margin: 0;
  font-weight: 400;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 780px) {
  .profile__info-occupation {
    margin: 0 auto;
    max-width: 282px;
    margin-top: 14px;
    margin-bottom: 36px;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
  }
}

.profile__info-add-button {
  padding: 0;
  width: 150px;
  height: 50px;
  background-color: transparent;
  background-image: url(/react-mesto-auth/static/media/add-button.b2fce4ac.svg);
  background-position: center;
  background-repeat: no-repeat;
  border: 2px solid #ffffff;
  box-sizing: border-box;
  border-radius: 2px;
  align-self: center;
  cursor: pointer;
}

.profile__info-add-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 425px) {
  .profile__info-add-button {
    width: 282px;
    height: 50px;
  }
}

.gallery {
  margin: 0 auto;
  max-width: 69%;
  padding: 47px 0 67px 0;
  overflow: hidden;
}

@media screen and (max-width: 425px) {
  .gallery {
    max-width: 88%;
    padding: 36px 0 48px 0;
  }
}

.gallery__elements {
  padding: 0;
  margin: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, 282px);
  grid-gap: 20px 17px;
  gap: 20px 17px;
  max-width: 100%;
  justify-content: center;
}

.gallery__element {
  margin: 0 auto;
  max-width: 100%;
  height: 361px;
  background-color: #fff;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  display: grid;
}

.gallery__element-image {
  position: relative;
  width: 282px;
  height: 282px;
  object-fit: cover;
  overflow: hidden;
}
.gallery__element-image:hover {
  opacity: 0.8;
  cursor: pointer;
}

.gallery__element-title {
  margin: 0;
  max-width: 197px;
  font-size: 24px;
  font-weight: 900;
  line-height: 29px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.gallery__element-like {
  padding: 0;
  width: 22px;
  height: 19px;
  background-image: url(/react-mesto-auth/static/media/like-button.68fe0213.svg);
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.gallery__element-like:hover {
  opacity: 0.5;
}

.gallery__element-like_active {
  background-image: url(/react-mesto-auth/static/media/Union.3d813f81.svg);
}

.gallery__element-description {
  padding: 25px 15px 31px 21px;
  max-width: 282px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.gallery__element-delete {
  position: absolute;
  top: 20px;
  right: 20px;
  background-image: url(/react-mesto-auth/static/media/Trash.4831282e.svg);
  width: 18px;
  height: 19px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: top;
  border: none;
  outline: none;
  cursor: pointer;
  transition: opacity 0.5s ease-in;
  visibility: hidden;
}
.gallery__element-delete:hover {
  opacity: 0.6;
  cursor: pointer;
}

.gallery__element-delete_displayed {
  visibility: visible;
}

.gallery__element-like-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gallery__element-like-counter {
  width: 10px;
  height: 13px;
  margin-top: 3px;
  font-size: 13px;
  font-weight: 400;
}

.footer {
  padding-bottom: 60px;
  margin: 0 auto;
  max-width: 69%;
  font-weight: 400;
  font-size: 18px;
  line-height: 21.78px;
  color: #545454;
}
@media screen and (max-width: 425px) {
  .footer {
    font-size: 14px;
    line-height: 16.94px;
    padding-bottom: 36px;
    max-width: 88%;
  }
}

.footer__copyrights {
  padding: 0;
  margin: 0;
}

.popup {
  display: flex;
  position: fixed;
  left: 0%;
  top: 0%;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s linear, opacity 0.5s linear;
  cursor: pointer;
}

.popup__container {
  position: relative;
  width: 430px;
  background-color: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

@media screen and (max-width: 600px) {
  .popup__container {
    width: 282px;
  }
}

.popup_opened {
  display: flex;
  visibility: visible;
  opacity: 1;
}

.popup__close-button {
  position: absolute;
  right: -36px;
  top: -36px;
  width: 32px;
  height: 32px;
  padding: 0;
  background: #ffffff;
  background-image: url(/react-mesto-auth/static/media/close-icon.cb0f2bbf.svg);
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
}

.popup__close-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 600px) {
  .popup__close-button {
    right: 0px;
    top: -28px;
    width: 20px;
    height: 20px;
  }
}

.popup__title {
  margin: 34px 64px 54px 36px;
  width: 77%;
  font-weight: 900;
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
}

@media screen and (max-width: 600px) {
  .popup__title {
    margin: 25px 22px 75px 22px;
    width: 84%;
    font-size: 18px;
    line-height: 22px;
  }
}

.popup__title_sign {
  margin-bottom: 20px;
  color: white;
  text-align: center;
}

.popup__input_sign {
  margin-top: 30px !important;
  border-bottom: 2px solid #cccccc !important;
  background-color: transparent;
  color: #cccccc;
}

.popup__container_sign {
  margin-top: 60px;
  background-color: transparent;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.popup__input {
  width: 83%;
  margin: 0 36px 0 36px;
  padding-bottom: 13px;
  height: 14px;
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 600px) {
  .popup__input {
    margin: 0px 22px 0px 22px;
  }
}

.popup__submit-button {
  width: 83%;
  height: 50px;
  margin: 31px 36px 37px 36px;
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  background: #000000;
  border-radius: 2px;
  border: 0;
  cursor: pointer;
  text-align: center;
  color: #ffffff;
}

.popup__submit-button:hover {
  opacity: 0.8;
}

@media screen and (max-width: 600px) {
  .popup__submit-button {
    margin: 15px 22px 25px 22px;
    height: 46px;
    font-size: 14px;
    line-height: 17px;
  }
}

.popup__figure {
  position: relative;
  max-width: 75vw;
  max-height: 75vh;
  margin: 0;
  background-color: rgb(0, 0, 0, 0.9);
}

.popup__image {
  width: 100%;
  max-height: 75vh;
}

@media screen and (max-width: 450px) {
  .popup__image {
    max-height: 299px;
  }
}

.popup_dark {
  background-color: rgba(0, 0, 0, 0.9);
}

.popup__image-title {
  margin-top: 10px;
  font-family: "Inter", Arial, sans-serif;
  color: #ffffff;
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
  text-align: center;
}

.popup__input-error {
  display: block;
  min-height: 30px;
  margin-right: 36px;
  margin-left: 36px;
  font-weight: normal;
  font-size: 12px;
  line-height: 14.52px;
  color: #ff0000;
  visibility: hidden;
}

.popup__input-error_active {
  visibility: visible;
}

.popup__submit-button_disabled {
  border: 1px solid #000;
  color: #000;
  opacity: 0.2;
  background-color: #fff;
}

.popup__submit-button_sign {
  background-color: #ffffff;
  margin-top: 216px;
  margin-bottom: 0px;
  color: #000000;
}

.popup__input_type_error {
  border-bottom: 1px solid #f00;
}

.popup__info-image {
  margin-top: 60px;
  width: 120px;
  height: 120px;
}

.popup__info-title {
  margin: 32px 36px 60px 36px;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #000000;
}

.popup__container_info {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}



.register__signin {
  margin-top: 15px;
  color: #ffffff;
  font-weight: 400;
  font-size: 14px;
  line-height: 16.94px;
  text-decoration: none;
}

.sign {
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.5);
  align-items: center;
  justify-content: center;
}



